.container {
  padding: 15px;
}

.back {
  display: block;
  padding: 2px 2px;
  margin-top: 60px;
  margin-bottom: 20px;
  border-radius: 8px;
  background-color: rgb(250, 250, 250);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  color: rgb(100, 100, 100);
  border: 2px solid;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  width: 80px;
}

.back:hover,
.back:focus {
  background-color: skyblue;
}

.links {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
  margin: 0 -15px;
  padding: 0 15px;
  color: aliceblue;
  background-color: skyblue;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.link_item {
  color: black;
  text-decoration: none;
  /* color: inherit; */
}

.link_item :hover {
  color: whitesmoke;
}
